<script>
import Nav from "./components/Nav.vue";
import Footer from "./components/Footer.vue";
import photosData from "./data/photosData";

console.log(photosData[1].name)


export default {
  name: "App",
  data(){
    return{
      photosData_: photosData,
      photosDataOri: [...photosData],
      modalOpen: false,
      choice: 0,
    }
  },
  methods: {
    sortDate(){
      this.photosData_.sort(function (a, b) {
        return b.date - a.date;
      })
    },
    sortId() {
      this.photosData_ = [...this.photosDataOri]
    },
  },
  components: {
    Nav,
    Footer,
  },
};
</script>

